<script>
import {get} from "@/api/http";
import SysLogo from "@/components/logo.vue";

export default {
  name:"SysIndex",
  components: {SysLogo},
  data(){
    return{
      infos:{},
      disks:{
        usage:0,
        total:"0",
        disk_usage:0
      },
      msg:true,
    }
  },
  methods:{
    init(){
      get('/info').then(res=>{
        if (this.msg){
          this.$message.success("获取数据成功")
        }
        this.msg = false
        if (res === null){
          this.$message.error("获取数据失败")
          return
        }
        this.infos = res
        this.disks = res['disk']
      }).catch(()=>{
        this.$message.error("请求失败")
      })
    }
  },
  created() {
    setInterval(this.init, 3000);
  },
}
</script>

<template>
  <div class="common-layout">
    <el-container>
      <el-header style="background-color: #e5e7ec;display: flex; justify-content: flex-start;">
        <SysLogo></SysLogo>
      </el-header>
      <el-main style="background-color: #f7f7f7;" v-loading="msg">
        <div style="display: flex;align-items: center;justify-content: center;">
          <el-card class="box-card">
            <template #header>
              <div class="card-header">
                <span>{{infos['hostname']}}</span>
                <el-dropdown>
                  <el-button type="primary" >
                    操作<el-icon class="el-icon--right"><arrow-down /></el-icon>
                  </el-button>
                  <template #dropdown>
                    <el-dropdown-menu>
                      <el-dropdown-item icon="Link" @click="this.$message.success('此功能正在完善中')">远程连接</el-dropdown-item>
                    </el-dropdown-menu>
                  </template>
                </el-dropdown>
              </div>
            </template>
            <div>
              <p><span class="key">主机名:</span><span class="value">{{infos['hostname']}}</span></p>
              <p><span class="key">系统平台:</span><span class="value">{{infos['system_type']}}</span></p>
              <p><span class="key">系统版本:</span><span class="value">{{infos['os']}}</span></p>
              <p><span class="key">CPU:</span><span class="value">{{infos['cpu']}}</span></p>
              <el-progress
                  :text-inside="true"
                  :stroke-width="24"
                  :percentage="infos['cpu_usage']"
              />
              <p><span class="key">内存:</span><span class="value">{{infos['memory']}}</span></p>
              <el-progress
                :text-inside="true"
                :stroke-width="24"
                :percentage="infos['memory_usage']"
              />
              <p><span class="key">系统盘:</span><span class="value">{{disks['usage']}} / {{disks['total']}}</span></p>
              <el-progress
                  :text-inside="true"
                  :stroke-width="24"
                  :percentage="disks['disk_usage']"
              />
              <p><span class="key">系统运行时长:</span><span class="value">{{infos['uptime']}}</span></p>
              <p><span class="key">网络信息:</span></p>
              <div style="padding: 10px">
                <div style="margin: 5px" v-for="(value, key) in infos['networks']" :key="key">
                  <span class="key">{{ key }}:</span><span class="value">{{ value }}</span>
                </div>
                <el-divider />
              </div>
              <p><span class="key">上/下行:</span></p>
              <div style="padding: 10px">
                <div v-for="ipInfo in infos['network_traffic']" :key="ipInfo.ip[0]" style="margin: 5px">
                  <span class="value">
                    net: {{ ipInfo['net'] }}<el-divider direction="vertical" />ip: {{ ipInfo.ip[0] }}<br>
                    <el-button-group>
                      <el-button icon="Upload" text>{{ ipInfo['up'] }}</el-button>
                      <el-button icon="Download" text>{{ ipInfo['down'] }}</el-button>
                    </el-button-group>
                  </span>
                </div>
                <el-divider></el-divider>
              </div>
            </div>
          </el-card>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<style scoped lang="scss">

.card-header {
  display: flex;
  justify-content: space-between;
}


.box-card {
  border-radius: 12px;
  width:100%;
  margin: 5px;
}
.key{
  color: #303133;
  margin-right: 5px;
}
.value{
  color: #909399;
}
</style>